<template>
  <div class="imgIndex">
    <i class="btn custom-icon" @click="goBack" ></i> 
    <!-- <i class="custom-icon"></i> -->
    <!--  -->
    <!-- <img
      class="img1"
      width="40%"
      height="40%"
      v-for="(image, index) in userObj.imgList"
      :key="index"
      :src="getImageUrl(image)"
      :style="getImageStyle(index)"
      alt=""
    /> -->

    <div>
      <img
        :class="userObj.id != 3 ? 'img1' : 'img11'"
        :src="getImageUrl(userObj.imgList[0])"
        alt=""
      />
      <img
        :class="userObj.id != 3 ? 'img2' : 'img22'"
        :src="getImageUrl(userObj.imgList[1])"
        alt=""
      />
      <img
        :class="userObj.id != 3 ? 'img3' : 'img33'"
        :src="getImageUrl(userObj.imgList[2])"
        alt=""
      />
    </div>
  </div>
</template>
<script>
export default {
  name: "imgIndex",
  data() {
    return {
      userObj: {},
    };
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    // 获取存储的字符串
    const storedUser = sessionStorage.getItem("data");

    if (storedUser) {
      // 将字符串解析为对象
      this.userObj = JSON.parse(storedUser);
      console.log(this.userObj); // { name: "John", age: 30 }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1); // 返回上一个页面
    },
    getImageUrl(imgPath) {
      // 在 Vue 项目中，通过 require() 动态加载图片log
      return require(`@/assets/${imgPath}`);
    },
    getImageStyle(index) {
      // 根据索引动态生成图片的样式
      const styles = {
        display: "block",
        "margin-left": `${index * 30}%`, // 每张图片左侧偏移量
        "margin-top": `${index * -15}%`, // 所有图片的上方偏移量
        "z-index": `${index + 2}`, // 设置不同的 z-index 层级
      };
      return styles;
    },
  },
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  computed: {}, // 计算属性
};
</script>
 
<style scoped >
.imgIndex {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between; /* 左右分布 */
  align-items: center; /* 垂直居中 */
  padding: 20px;
  background: #f2f2f2; /* 容器的背景色 */
  color: white;
}
img {
  /* border: 1px solid #000; */
  display: block;
}
.img1 {
  position: absolute;
  top: 3%;
  left: 2%;
  width: 35%;
  height: 35%;
}
.img11 {
  width: 33% !important;
  height: 100% !important;
  display: inline-block !important;
  padding: 0 20px;
}
.img2 {
  position: absolute;
  top: 33%;
  left: 33%;
  width: 35%;
  height: 35%;
}
.img22 {
  width: 33% !important;
  height: 100% !important;
  display: inline-block !important;
  padding: 0 20px;
}
.img3 {
  position: absolute;
  bottom: 3%;
  right: 2%;
  width: 35%;
  height: 35%;
}
.img33 {
  width: 33% !important;
  height: 100% !important;
  width: 28%;
  display: inline-block !important;
  padding: 0 20px;
}
.btn {
  position: absolute;
  top: 48%;
  left: 40px;
  width: 50px; /* 设置按钮图标大小 */
  height: 50px;
  padding: 0; /* 去掉内边距 */
}
.custom-icon {
  background: url('~@/assets/go4.png') 
  no-repeat center center;
  width: 80px; /* 设置按钮图标大小 */
  height: 50px;
  background-size: 200% 200%; 
  object-fit: contain; /* 确保图片按比例自适应 */
  z-index: 99999;
  margin-left: -15px;
}


</style>


